/* CategoriesCarousel.css */
.categories-carousel {
    display: flex;
    overflow-x: auto;
    gap: 20px;
    padding: 20px;
    background-color: #f8f9fa;
  }
  
  .category-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 120px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .category-card img {
    height: 80px;
    margin-bottom: 10px;
  }
  
  .category-card p {
    font-size: 14px;
    font-weight: bold;
  }
  