/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #007bff;
    padding: 10px;
  }
  
  .navbar__logo img {
    height: 50px;
  }
  
  .navbar__links {
    display: flex;
    gap: 20px;
  }
  
  .navbar__links a {
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .navbar__search input {
    padding: 5px;
    border: none;
    border-radius: 5px;
  }
  
  .navbar__icons a {
    margin-left: 15px;
    color: white;
    text-decoration: none;
  }
  